$cell-size:100px;
$gap-size:1px;
$calendar-pading:10px;
$width-calendar: ($cell-size*7)+($gap-size*6)+($calendar-pading*2);
$width-day:100px;
$height-day:100px;
$color-day-cell:#eee;
$color-border-cell:black;
$colorHover-day-cell:pink;
$week:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;
